<template>
  <div class="itemBox">
    <p class="title">
      富文本&nbsp;
      <span>以实际效果为准，左侧预览仅供参考</span>
    </p>
    <el-form ref="form" :model="itemData" size="mini">
      <el-form-item label="背景颜色" class="bgColor bottomLine">
        <el-color-picker v-model="itemData.backgroundColor" size="mini"></el-color-picker>
        <el-button type="text" @click="itemData.backgroundColor='#fff'">重置</el-button>
      </el-form-item>
      <el-form-item label="是否全屏显示" class="bottomLine">
        <el-radio-group v-model="itemData.isFullScreen">
          <el-radio :label="0">全屏展示</el-radio>
          <el-radio :label="1">不全屏展示</el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item>
        <input
          style="display:none;"
          type="file"
          name="file"
          id="file"
          accept="image/jpeg, image/png, image/jpg, image/gif"
          @change="getimg($event)"
        />
        <div id="editor" style="height:300px;"></div>
      </el-form-item>
    </el-form>
    <!-- {{itemData}} -->

    <el-dialog
      title="图库列表"
      :visible.sync="dialogVisible"
      width="76%"
      :close-on-click-modal="false"
      @close="submitFormqx"
      :append-to-body="true"
      class="add-dialog"
      top="6vh"
    >
      <imageMaterial
        :limitType="limitType"
        :getPost="getPost"
        :multiple="true"
        :totalNum="5"
        :selectNum="selectNum"
        @onlyclose="submitFormqx"
        @subMit="submitForm"
      ></imageMaterial>
    </el-dialog>
  </div>
</template>

<script>
import VueUeditor from "vue-ueditor";
import imageMaterial from "@/components/shopMaterial/imageMaterial";
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  components: {
    VueUeditor,
    imageMaterial
  },
  data() {
    return {
      //富文本
      ue1: "ue1",
      dialogVisible: false,
      getPost: true,
      limitType: false,
      selectNum: 0
    };
  },

  methods: {
    //弹窗取消按钮
    submitFormqx() {
      this.dialogVisible = false;
      this.getPost = false;
    },
    //弹窗确定按钮
    submitForm(e) {
      console.log(e);
      if (e.length == 0) {
        this.$message.error("请选择图片");
        return;
      }
      let str = "";
      e.forEach(item => {
        str += `<p><img src="${item.imageUrl}" alt=""></p>`;
      });
      this.ue1.execCommand("inserthtml", str);
      console.log("上传给富文本的图片", str);
      this.dialogVisible = false;
      this.getPost = false;
    }
  },
  mounted() {
    let _this = this;
    this.$nextTick(() => {
      // 注册上传图片按钮
      UE.registerUI("diyimg", function(editor, uiName) {
        var btn = new UE.ui.Button({
          name: uiName, //按钮的名字
          title: "添加图片", //提示
          cssRules: "background-position: -380px 0;",
          elementPathEnabled: false,
          onclick: function() {
            _this.selectNum = 0;
            _this.getPost = true;
            _this.dialogVisible = true;
            _this.limitType = false;
          }
        });
        return btn;
      });
      this.ue1 = UE.getEditor("editor", {
        zIndex: 9,
        elementPathEnabled: false
      });
      console.log("实例化成功！");

      // 回显数据
      this.ue1.addListener(
        "ready",
        function() {
          this.ue1.setContent(this.itemData.html);
        }.bind(this)
      );

      // 富文本框的数据双向绑定
      this.ue1.addListener(
        "contentChange",
        function() {
          this.itemData.html = this.ue1.getContent();
        }.bind(this)
      );
    });
    setTimeout(() => {}, 3000);
  },
  destroyed() {
    this.ue1.destroy();
    console.log("销毁");
  }
};
</script>

<style scoped lang="less">
.active {
  border: 1px solid #000;
}
.title {
  padding: 20px 18px 24px 18px;
  border-bottom: 1px solid #eee;
  color: #333;
  font-size: 16px;
  font-weight: 700;
  text-align: left;
  span {
    height: 14px;
    font-size: 12px;
    color: rgba(153, 153, 153, 1);
    line-height: 14px;
    letter-spacing: 0px;
    font-weight: 100;
  }
}

.itemBox /deep/ .el-form {
  margin: 18px 24px;
  .el-form-item__label {
    display: block;
    float: inherit;
    text-align: inherit;
    padding: 0;
    color: #222;
    font-weight: 100;
    margin-bottom: 12px;
  }
  .el-input__inner {
    height: 34px;
    border-radius: 2px;
  }
  .el-form-item__error {
    padding-top: 5px;
  }
  .el-radio__label {
    font-size: 12px;
    color: #333;
  }
  .bgColor .el-form-item__content {
    display: flex;
    align-items: center;
    .el-button {
      margin-left: 10px;
    }
  }
  .bottomLine {
    border-bottom: 1px solid #eee;
    padding-bottom: 24px;
  }
}
.edui-default .edui-toolbar .edui-combox .edui-combox-body {
  height: 22px;
}
</style>
